@import "../../global.scss";
.contact{
  
    display: flex;
    flex-direction: column;
    align-items: center;
    color:black;
    padding-top: 40px;
    background-repeat: no-repeat;
    background-size: 100%;
    position: relative;
   
    
    @include mobile{
        flex-direction: column;
        font-size: 10px;
        background-size: 100% 100%;
    }

    .about{
        position: absolute;
        margin-top: 390px;
        text-align: center;
        color:black;

        @include mobile{
            font-size: 9px;
            margin-top: 380px;
        }

        a:link {
            color: black;
            background-color: transparent;
            
          }
          a:visited {
            color: black;
            background-color: transparent;
            
          }
       

        #facebook:hover {
            background-color: rgb(112, 112, 233);
        }
        #instagram:hover{
            background-color: orange;
        }
          
    }
    
}